export default class Section {
  constructor ({renderer}, containerSelector) {
    // renderer - ÑÑÐ½ÐºÑÐ¸Ñ ÐºÐ¾Ð»Ð±ÑÐº, Ð¾ÑÐ²ÐµÑÐ°ÑÑÐ°Ñ Ð·Ð° cÐ¾Ð·Ð´Ð°Ð½Ð¸Ðµ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑÐ¾Ð² ÐºÐ°ÑÑÐ¾ÑÐµÐº Ð¸ Ð¸Ñ Ð²ÑÑÐ°Ð²ÐºÑ Ð² ÑÐ°Ð·Ð¼ÐµÑÐºÑ
    this._renderer = renderer;
    this._container = document.querySelector(containerSelector);
  };

  // Ð¾ÑÐ²ÐµÑÐ°ÐµÑ Ð·Ð° Ð¾ÑÑÐ¸ÑÐ¾Ð²ÐºÑ Ð²ÑÐµÑ ÑÐ»ÐµÐ¼ÐµÐ½ÑÐ¾Ð²
  renderItems(cards) {
    cards.forEach(card => {
      this._renderer(card);
    });
  };

  addItemToTheEnd(card) {
    this._container.append(card);
  };

  addItemToTheStart(card) {
    this._container.prepend(card);
  };
}
