     // ÐÐ°Ð¼ÐµÐ½Ð¸Ð¼ ÑÐ¾Ð´ÐµÑÐ¶Ð¸Ð¼Ð¾Ðµ span Ñ Ð¾ÑÐ¸Ð±ÐºÐ¾Ð¹ Ð½Ð° Ð¿ÐµÑÐµÐ´Ð°Ð½Ð½ÑÐ¹ Ð¿Ð°ÑÐ°Ð¼ÐµÑÑ.
    errorElement.textContent = errorMessage;
