      this._buttonElement.classList.add(this._settings.inactiveButtonClass);
      // Ð§ÑÐ¾Ð±Ñ Ð½Ðµ Ð±ÑÐ»Ð¾ Ð²Ð¾Ð·Ð¼Ð¾Ð¶Ð½Ð¾ÑÑÐ¸ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÑÑ Ð¿ÑÑÑÑÐµ ÐºÐ°ÑÑÐ¾ÑÐºÐ¸ Ð¿Ð¾ Ð½Ð°Ð¶Ð°ÑÐ¸Ñ Ð½Ð° Enter Ð¿ÑÐ¸ Ð²Ð¸Ð·ÑÐ°Ð»ÑÐ½Ð¾ Ð·Ð°Ð±Ð»Ð¾ÐºÐ¸ÑÐ¾Ð²Ð°Ð½Ð½Ð¾Ð¹ ÐºÐ½Ð¾Ð¿ÐºÐµ Ð½ÑÐ¶Ð½Ð¾ Ð´Ð¾Ð±Ð°Ð²Ð¸ÑÑ Ð°ÑÑÐ¸Ð±ÑÑ Disable.
      this._buttonElement.setAttribute('disabled', true);
