 // ÑÐ¾Ð·Ð´Ð°Ð½Ð¸Ðµ Ð½Ð¾Ð²Ð¾Ð¹ ÐºÐ°ÑÑÐ¾ÑÐºÐ¸ Ñ Ð¿Ð¾Ð¼Ð¾ÑÑÑ ÐºÐ»Ð°ÑÑÐ° Card
const createCard = (data) => {
  const card = new Card ({
    data,
    handleImgCardClick: (data) => {
      picturePopup.open(data);
    },
    handleDeleteBtnCardClick: () => {
      patternCard = card;
      popupConfirmDelete.open(data);
    },
    handleLikeBtnCardClick: (data) => {
      api.putLike(data)
      .then((res) => {
      card.getlikesCount(res)
      })
      .catch((err) => {
        console.log(err);
      })
    },
    handleDislikeBtnCardClick: (data) => {
      api.deleteLike(data)
      .then((res) => {
      card.getlikesCount(res)
      })
      .catch((err) => {
        console.log(err);
      })
    },
    templateSelector: '.element-template'
  },
  ownerId);

  const cardElement = card.generateCard();

  return cardElement;
}

